body {
  font-family: "jura", sans-serif;
  font-size: 1.6 rem;
  line-height: 1.8;
  min-width: 480px;
  background-color: #1d1d1d;
}
body.body-scroll {
  padding-top: 68.39px;
}

p {
  font-size: 2rem;
  color: #FFFFFF;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
  text-align: center;
}

a {
  color: #FFFFFF;
  -webkit-transition: color 0.3s;
  -o-transition: color 0.3s;
  transition: color 0.3s;
  text-decoration: none;
}

a:hover {
  color: #d21211;
}

h1, h2, h3 {
  font-family: "jura", sans-serif;
  font-weight: 300;
  text-align: center;
}

h1 {
  color: #2b2b2b;
  margin: 0;
  font-weight: 900;
  font-size: 5rem;
  position: relative;
  -webkit-text-stroke: 2px white;
}

h2 {
  color: #d21211;
  font-weight: 400;
  font-size: 3.4rem;
  left: -10px;
  font-size: 7.3rem;
  line-height: 6.7rem;
}

h2 .anim,
h1 .anim {
  font-size: 8.3rem;
  font-family: "coolvetica";
  letter-spacing: -10px;
  opacity: 0;
  display: inline-block;
  -webkit-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

h1 .anim:hover,
h2 .anim:hover {
  color: #d21211;
  transition: all 0.1s;
}

h2 .anim:hover {
  text-shadow: 0.1px 0.1px 0.1px white, 0 0 2em white, 0 0 0.5em white;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.neon {
  font-family: "coolvetica", Helvetica, Arial, Lucida, sans-serif;
  font-size: 4.3rem;
  text-shadow: 0.5px 0.5px 0.1px black, 0 0 2em black, 0 0 0.5em black;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
  object-fit: fill;
}

.rojo {
  background-color: red;
}

.red-text {
  font-size: 1.6rem;
  font-weight: 600;
  letter-spacing: 4px;
  margin-top: 3rem;
  display: block;
  color: #d21211;
  font-size: 2.8rem;
}

.section-home {
  height: 100vh;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.section-full {
  min-height: 100vh;
  padding: 9rem 3rem;
  position: relative;
  -webkit-transition: opacity 0.4s;
  -o-transition: opacity 0.4s;
  transition: opacity 0.4s;
}

.text-zone h1, .text-zone h2 {
  text-align: start;
  line-height: 7rem;
}
.text-zone h1 {
  font-size: 6rem;
}
.text-zone h2 {
  font-size: 5rem;
}
.text-zone .final-leter {
  padding-right: 2.5rem;
}

.section-full .text-zone {
  padding-left: 3rem;
}

.anim_rubberBand {
  animation: rubberBand 0.5s linear both;
}

.anim_slideInDown {
  animation: slideInDown 0.2s linear both;
}

.anim_slideOutUp {
  animation: slideOutUp 1s linear both;
}

#particles-js {
  background: url(../img/Cabecera6.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: cover;
  background-size: cover;
  width: 100%;
  opacity: 0.8;
  position: relative;
}

/*!
 * animate.css -http://daneden.me/animate
 * Version - 3.5.2
 * Licensed under the MIT license - http://opensource.org/licenses/MIT
