body {
  background: url(../img/code-1.jpg);
  background-attachment: cover;
  background-size: cover;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.data {
  width: 80%;
  min-height: 100%;
  padding-top: 10rem;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.data .input {
  min-height: 10rem;
  min-width: 60rem;
  background: #2b2b2b;
  font-size: 2rem;
  color: #FFFFFF;
  border: 0;
  box-sizing: border-box;
}
.data .botones {
  display: flex;
  position: relative;
  justify-content: space-around;
  width: 40%;
  margin-top: 3rem;
}
.data .botones .codificar {
  background-color: #2b2b2b;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 1.5rem;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  border: none;
  border-radius: 1rem;
}
.data .botones .codificar:hover {
  background-color: #121212;
  cursor: pointer;
}
.data .botones .decodificar {
  background-color: #FFFFFF;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 1.5rem;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  border: none;
  border-radius: 1rem;
  color: #2b2b2b;
}
.data .botones .decodificar:hover {
  background-color: #e6e6e6;
  cursor: pointer;
}

.input__label--hoshi::before,
.input__label--hoshi::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
}

.input__label--hoshi::after {
  margin-top: 2px;
  border-bottom: 2px solid #fd0808;
  -webkit-transform: translate3d(-100%, 0, 0);
  transform: translate3d(-100%, 0, 0);
  -webkit-transition: -webkit-transform 0.3s;
  transition: -webkit-transform 0.3s;
  -o-transition: transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}

.input__field--hoshi:focus + .input__label--hoshi::after,
.input--filled .input__label--hoshi::after {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.required .input__field--hoshi + .input__label--hoshi::after,
.required .input--filled .input__label--hoshi::after {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  border-color: red;
}

.input__field--hoshi:focus + .input__label--hoshi .input__label-content--hoshi,
.input--filled .input__label-content--hoshi {
  -webkit-animation: anim-1 0.3s forwards;
  animation: anim-1 0.3s forwards;
}